body {
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
  font-size: 16px;
}
.UMC-widget-wrapper {
  width: 500px;
  padding-bottom: 10px;
  position: relative;
}
.UMC-widget-steps__header {
  padding-bottom: 40px;
  padding-top: 20px;
}

.UMC-widget-loading-screen {
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: #00000050;
}
.UMC-widget-btn-area {
  padding-right: 20px!important;
}
.UMC-widget-calendar-wrapper {
  /*width: 1000px;*/
}
.UMC-widget-calendar-block {
  min-height: 260px;
}
.UMC-widget-calendar-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.UMC-widget-calendar__header {
  margin-bottom: 20px;
}
.UMC-widget-title {
  font-size: 1.1rem;
}
.UMC-widget-calendar-header__text {
  min-width: 140px;
  text-align: center;
  margin: 0 20px;
}

.UMC-widget-calendar__grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
}

.UMC-widget-calendar__box {
  box-sizing: border-box;
  display: flex;
  justify-self: center;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 30px;
  user-select: none;
  font-size: 0.8125rem;
  border-radius: 4px;
}

.UMC-widget-calendar__box_free {
  cursor: pointer;
  transition: all ease 0.1s;
  color: #1976d2;
  border: 1px solid rgba(25, 118, 210, 0.3);
}

.UMC-widget-calendar__box_free:hover {
  background-color: rgba(25, 118, 210, 0.3);
}

.UMC-widget-calendar__box_selected {
  background-color: #1976d2;
  color: #fff;
}

.UMC-widget-calendar__box_header {
  font-size: 1rem;
}


.UMC-widget-time {
  display: flex;
  flex-wrap: wrap;
  min-height: 100px;
  overflow: auto;
  align-items: flex-start;
}
.UMC-widget-time-block {
  padding-top: 20px;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}
.UMC-widget-time__title {
  font-weight: 500;
  margin-left: 10px;
  margin-bottom: 20px;
}
.UMC-widget-time__box {
  padding: 7px;
  margin: 2.5px 8px;
  border-radius: 4px;
  cursor: pointer;
  transition: all ease 0.1s;
  color: #1976d2;
  border: 1px solid rgba(25, 118, 210, 0.3);
}

.UMC-widget-time__box:hover {
  background-color: rgba(25, 118, 210, 0.3);
}

.UMC-widget-time__box_selected {
  background-color: #1976d2;
  color: #fff;
}

.UMC-widget-input-error {
  color: #d32f2f!important;
}

.UMC-widget-calendar__column {
  width: 120px;
  padding: 5px;
}

.UMC-widget-calendar__column-title {
  font-size: 1rem;
  text-align: center;
  font-weight: 400;
}

.UMC-widget-calendar__column-text {
  font-size: 0.8125rem;
  text-align: center;
}

.UMC-widget-calendar__column-text_grey {
  color: grey;
}

.UMC-widget__btn-exit {
  position: absolute!important;
  right: 20px;
  top: 16px;
}

.UMC-widget__personal-btn {
  margin-top: 5px!important;
  justify-content: flex-start!important;
}

@media screen and (max-width: 1024px) {
  .UMC-widget-calendar__column-content {
    max-height: 500px;
    overflow: auto;
  }
}
@media screen and (max-width: 768px) {
  .UMC-widget-calendar__column-content {
    max-height: 300px;
  }
  .UMC-widget-calendar__column {
    width: 100%;
  }
  .UMC-widget-calendar__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 450px) {
  .UMC-widget-wrapper {
    width: 100%;
  }
  .UMC-widget-calendar__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .UMC-widget-calendar-header__text {
    min-width: 120px;
    font-size: 1rem;
  }
}